<template>
  <!--  class starType  class -->
  <div class="star" :class="starType">
    <!--  class itemClass v-for -->
    <!-- 3.5 [CLS_ON, CLS_ON, CLS_ON, CLS_HALF, CLS_OFF, CLS_OFF] -->
    <span v-for="(itemClass,index) in itemClasses" :class="itemClass" class="star-item" :key="index"></span>
  </div>
</template>

<script>
const LENGTH = 5 //  start 
const CLS_ON = 'on' // CLS_ON  
const CLS_HALF = 'half' // CLS_HALF  
const CLS_OFF = 'off' // CLS_OFF  

export default {
  props: {
    size: {
      type: Number
    },
    score: {
      type: Number
    }
  },
  computed: {
    starType () {
      return 'star-' + this.size
    },
    itemClasses () {
      let result = []
      const score = Math.floor(this.score * 2) / 2
      // Math.floor() 
      // 
      // 4.0  score  4.0 
      // 4.3  score  4.5 
      // 4.5  score  4.5 
      // 4.8  score  4.5 
      // 5.0  score  5.0 
      const hasDecimal = score % 1 !== 0
      const integer = Math.floor(score) // 
      // 3.3 result  3  CLS_ON
      for (let i = 0; i < integer; i++) {
        result.push(CLS_ON)
      }
      // 3.3 hasDecimal  0.3 true4 CLS_HALF
      if (hasDecimal) {
        result.push(CLS_HALF)
      }
      //  CLS_OFF
      while (result.length < LENGTH) {
        result.push(CLS_OFF)
      }
      return result
    }
  }
}
</script>

