<template>
  <div class="tab">
    <cube-tab-bar
      :showSlider=true
      :useTransition=false
      v-model="selectedLabel"
      :data="tabs"
      ref="tabBar"
      class="border-bottom-1px"
    >
    </cube-tab-bar>
    <div class="slide-wrapper">
      <cube-slide
        :loop=false
        :auto-play=false
        :show-dots=false
        :initial-index="index"
        ref="slide"
        @change="onChange"
        @scroll="onScroll"
        :options="slideOptions"
      >
      <!-- initial-index  props  initialIndex  tabItem -->
      <!-- @change  tab tablabel/value -->
      <!-- @scroll  Object {x, y} - -->
      <!-- :option better-scroll  -->
        <cube-slide-item v-for="(tab,index) in tabs" :key="index">
          <!--  tabItemtabItemtabItem propstab -->
          <!-- tabItemtabItemtabItem GoodsRatingsSeller -->
          <!-- tab.component tab.data  -->
          <component ref="component" :is="tab.component" :data="tab.data"></component>
        </cube-slide-item>
      </cube-slide>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'tab',
    props: {
      tabs: {
        type: Array,
        default() {
          return []
        }
      },
      initialIndex: {
        type: Number,
        default: 0
      }
    },
    data() {
      return {
        index: this.initialIndex, // tabItemindex
        slideOptions: {
          listenScroll: true,
          probeType: 3, // , probeType  3  momentum  scroll 
          directionLockThreshold: 0
          //  directionLockThreshold 
          // tab directionLockThreshold 
        }
      }
    },
    computed: {
      selectedLabel: {
        get() {
          return this.tabs[this.index].label
        },
        set(newVal) {
          this.index = this.tabs.findIndex((value) => {
            return value.label === newVal
          })
        }
      }
    },
    mounted () {
      this.onChange(this.index)
    },
    methods: {
      onChange (current) {
        this.index = current
        //  tabItem 
        //  Goods RatingsSeller
        //  tabItem 
        //  onChange  mounted 
        const instance = this.$refs.component[current] //  $refs 
        if (instance && instance.fetch) {
          instance.fetch() //  fetch  fetch() 
        }
      },
      onScroll (pos) {
        // pos 
        const tabBarWidth = this.$refs.tabBar.$el.clientWidth //  cube-tab-bar 
        const slideWidth = this.$refs.slide.slide.scrollerWidth //  cube-slide 
        const transform = -pos.x / slideWidth * tabBarWidth
        // pos.x -pos.x  cube-slide  cube-tab-bar  cube-tab-bar 
        this.$refs.tabBar.setSliderTransform(transform)
        // setSliderTransform  cube-tab-bar  transformX Number String
        //  showSlider  true
        // cube-tab-bar  transition  cube-slide  cube-tab-bar  useTransition  false
      }
    }
  }
</script>

<style lang="stylus" scoped>
  @import "~common/stylus/variable"

  .tab
    display: flex
    flex-direction: column
    height: 100%
    >>> .cube-tab
      padding: 10px 0
    .slide-wrapper
      flex: 1
      overflow: hidden
</style>
